
"use server";

import { revalidatePath } from "next/cache";
import { firestore } from "@/lib/firebase"; // Asegurándonos que este es el admin SDK
import { collection, addDoc, serverTimestamp } from "firebase/firestore";

/**
 * Guarda una nueva solicitud de turno en la base de datos.
 * @param {string} userId - El UID del cliente que solicita el turno.
 * @param {object} turnoData - Los datos del turno del formulario.
 * @returns {Promise<{success: boolean, error?: string}>}
 */
export async function solicitarTurno(userId, turnoData) {
  if (!userId) {
    return { success: false, error: "Usuario no autenticado." };
  }

  const { mascotaId, motivo, fecha, tipo } = turnoData;

  if (!mascotaId || !motivo || !fecha || !tipo) {
    return { success: false, error: "Faltan campos obligatorios." };
  }

  try {
    // Guardamos el turno en la colección principal `turnos`
    await addDoc(collection(firestore, "turnos"), {
        clienteId: userId,
        mascotaId: mascotaId,
        motivo: motivo,
        fechaSolicitada: fecha,
        tipo: tipo, // 'consulta' o 'peluqueria'
        estado: 'pendiente', // Los turnos inician como pendientes de confirmación
        creadoEn: serverTimestamp(), // Fecha de creación del documento
    });

    // Podríamos revalidar una página de "mis turnos" si existiera
    // revalidatePath("/mis-turnos");

    return { success: true };

  } catch (error) {
    console.error("Error al solicitar el turno:", error);
    return { success: false, error: "No se pudo procesar la solicitud de turno." };
  }
}
